import React from 'react';
import { Container } from './styles';

import HeaderForm from '../../components/HeaderForm';

class CadastroExercicio extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            muscle: '',
            exerciseName: '',
            exercises: [] // Array para armazenar os exerc√≠cios cadastrados
        };
    }

    handleMuscleChange = (event) => {
        this.setState({ muscle: event.target.value });
    }

    handleExerciseNameChange = (event) => {
        this.setState({ exerciseName: event.target.value });
    }

    handleSubmit = (event) => {
        event.preventDefault();
        // Adicionar o novo exerc√≠cio ao array de exerc√≠cios
        const newExercise = {
            muscle: this.state.muscle,
            exerciseName: this.state.exerciseName
        };
        this.setState(prevState => ({
            exercises: [...prevState.exercises, newExercise],
            muscle: '',
            exerciseName: ''
        }));
    }

    render() {
        return (
            <div>
                <HeaderForm />
                <Container>
                    <form onSubmit={this.handleSubmit}>
                        <div>
                            {/* Adicionar os valores no banco de dados */}
                            <label htmlFor="muscle">M√∫sculo:</label>
                            <select id="muscle" value={this.state.muscle} onChange={this.handleMuscleChange}>
                                <option value="">Selecione um m√∫sculo</option>
                                <optgroup label="M√∫sculos do peito">
                                    <option value="peitorais maior">Peitorais maior</option>
                                    <option value="peitorais menor">Peitorais menor</option>
                                </optgroup>
                                <optgroup label="M√∫sculos das costas">
                                    <option value="lat√≠ssimo do dorso">Lat√≠ssimo do dorso</option>
                                    <option value="trap√©zio">Trap√©zio</option>
                                    <option value="romboides">Romboides</option>
                                </optgroup>
                                <optgroup label="M√∫sculos dos ombros">
                                    <option value="deltoides">Deltoides</option>
                                </optgroup>
                                <optgroup label="M√∫sculos dos bra√ßos">
                                    <option value="b√≠ceps">B√≠ceps</option>
                                    <option value="tr√≠ceps">Tr√≠ceps</option>
                                    <option value="antebra√ßos">Antebra√ßos</option>
                                </optgroup>
                                <optgroup label="M√∫sculos das pernas">
                                    <option value="quadr√≠ceps">Quadr√≠ceps</option>
                                    <option value="isquiotibiais">Isquiotibiais</option>
                                    <option value="gl√∫teos">Gl√∫teos</option>
                                    <option value="panturrilhas">Panturrilhas</option>
                                </optgroup>
                                <optgroup label="M√∫sculos do abd√¥men">
                                    <option value="reto abdominal">Reto abdominal</option>
                                    <option value="obl√≠quos">Obl√≠quos</option>
                                    <option value="transverso">Transverso</option>
                                </optgroup>
                                <optgroup label="M√∫sculos do pesco√ßo">
                                    <option value="trap√©zio">Trap√©zio</option>
                                    <option value="esternocleidomast√≥ideo">Esternocleidomast√≥ideo</option>
                                </optgroup>
                            </select>
                        </div>
                        <div className="input-group">
                            <div className="input-block">
                                <label htmlFor="exerciseName">Nome do exerc√≠cio:</label>
                                <input type="text" id="exerciseName" value={this.state.exerciseName} onChange={this.handleExerciseNameChange} />
                            </div>
                            <button type="submit">Cadastrar</button>
                        </div>
                    </form>
                    <table>
                        <thead>
                            <tr>
                                <th>M√∫sculo</th>
                                <th>Nome do exerc√≠cio</th>
                            </tr>
                        </thead>
                        <tbody>
                            {this.state.exercises.map((exercise, index) => (
                                <tr key={index}>
                                    <td>{exercise.muscle}</td>
                                    <td>{exercise.exerciseName}</td>
                                </tr>
                            ))}
                        </tbody>
                    </table>
                </Container>
            </div>
        );
    }
}

export default CadastroExercicio;